/**
 * Wordpress Dependencies
 */
import {
	RichText,
	useBlockProps,
	__experimentalGetBorderClassesAndStyles as getBorderClassesAndStyles,
	__experimentalGetSpacingClassesAndStyles as getSpacingClassesAndStyles,
	getComputedFluidTypographyValue,
	getTypographyClassesAndStyles,
} from "@wordpress/block-editor";
import { omitBy, isEmpty, omit } from "lodash";

function Save(props) {
	const { attributes } = props;
	const { backgroundColor, backgroundGradient } = attributes;
	const borderProps = getBorderClassesAndStyles(attributes);
	const spacingProps = getSpacingClassesAndStyles(attributes);
	const typographyProps = getTypographyClassesAndStyles(attributes);

	const justificationClass =
		"justify-content-" + attributes?.layout?.justifyContent ?? "left";
	let blockProps = useBlockProps.save({
		className: "ghub-query-load-more " + justificationClass,
	});

	const borderStyle = borderProps?.style ?? {};
	const spacingStyle = spacingProps?.style ?? {};
	const typographyStyle = typographyProps.style ?? {};

	return (
		<div
			{...omit(
				{
					...blockProps,
					className: blockProps?.className
						.split(" ")
						.filter((className) => !className.includes("border"))
						.join(" "),
				},
				"style"
			)}
		>
			<RichText.Content
				tagName="a"
				value={attributes.label}
				style={omitBy(
					{
						backgroundColor: backgroundColor,
						backgroundImage: backgroundGradient,
						...borderStyle,
						...spacingStyle,
						...typographyStyle,
					},
					isEmpty
				)}
				className={`ghub_query_load_more_link wp-element-button${
					" " + typographyProps?.className ?? ""
				}${" " + borderProps?.className ?? ""}`}
			/>
		</div>
	);
}
export default Save;
