import { __ } from "@wordpress/i18n";
import { PanelBody, TextControl, ToggleControl } from "@wordpress/components";
import {
	useBlockProps,
	RichText,
	InspectorControls,
	useSetting,
	__experimentalUseBorderProps as useBorderProps,
	getTypographyClassesAndStyles as useTypographyProps,
	__experimentalGetSpacingClassesAndStyles as useSpacingProps,
	__experimentalColorGradientSettingsDropdown as ColorGradientSettingsDropdown,
	__experimentalUseMultipleOriginColorsAndGradients as useMultipleOriginColorsAndGradients,
} from "@wordpress/block-editor";
import { omitBy, isEmpty, omit } from "lodash";

import "./editor.scss";

export default function Edit(props) {
	const { attributes, setAttributes } = props;
	const { backgroundColor, backgroundGradient, autoTrigger } = attributes;
	const fluidTypographySettings = useSetting("typography.fluid");
	const borderProps = useBorderProps(attributes);
	const spacingProps = useSpacingProps(attributes);
	const typographyProps = useTypographyProps(
		attributes,
		fluidTypographySettings
	);
	const justificationClass =
		"justify-content-" + attributes?.layout?.justifyContent ?? "left";
	let blockProps = useBlockProps({
		className: "ghub-query-load-more " + justificationClass,
	});

	const colorGradientSettings = useMultipleOriginColorsAndGradients();
	const borderStyle = borderProps?.style ?? {};
	const typographyStyle = typographyProps.style ?? {};
	const spacingStyle = spacingProps?.style ?? {};

	return (
		<>
			<div
				{...omit(
					{
						...blockProps,
						className: blockProps?.className
							.split(" ")
							.filter((className) => !className.includes("border"))
							.join(" "),
					},
					"style"
				)}
			>
				<RichText
					tagName="a"
					value={attributes.label}
					style={omitBy(
						{
							backgroundColor: backgroundColor,
							backgroundImage: backgroundGradient,
							...borderStyle,
							...spacingStyle,
							...typographyStyle,
						},
						isEmpty
					)}
					className={`ghub_query_load_more_link wp-element-button${
						" " + typographyProps?.className ?? ""
					}${" " + borderProps?.className ?? ""}`}
					onChange={(newLabel) => setAttributes({ label: newLabel })}
					placeholder={__("Load More", "query-load-more")}
				/>
			</div>
			<InspectorControls>
				<PanelBody>
					<TextControl
						label="Loading Text"
						value={attributes.loadingText}
						onChange={(newText) => {
							setAttributes({ loadingText: newText });
						}}
					/>
					<ToggleControl
						checked={autoTrigger}
						help={__("Auto trigger when in viewport.", "query-load-more")}
						label={__("Auto Trigger", "query-load-more")}
						onChange={() => setAttributes({ autoTrigger: !autoTrigger })}
					/>
				</PanelBody>
			</InspectorControls>
			<InspectorControls group="color">
				<ColorGradientSettingsDropdown
					title={__("Color Settings", "gutenberghub-load-more")}
					panelId={props.clientId}
					settings={[
						{
							colorValue: backgroundColor,
							gradientValue: backgroundGradient,
							label: __("Background", "gutenberghub-load-more"),
							onColorChange: (newValue) => {
								setAttributes({ backgroundColor: newValue });
							},
							onGradientChange: (newValue) => {
								setAttributes({ backgroundGradient: newValue });
							},
						},
					]}
					{...colorGradientSettings}
				/>
			</InspectorControls>
		</>
	);
}
